<template>
    <div class="board-page">
        <div class="container">
            <!-- 헤더 섹션 -->
            <div class="board-header">
                <h1 class="page-title">자유게시판</h1>
            </div>

            <!-- 검색 및 필터 -->
            <div class="board-controls">
                <div class="search-box">
                    <input 
                        type="text" 
                        v-model="searchKeyword" 
                        placeholder="제목 또는 작성자를 검색하세요..."
                        class="search-input"
                    >
                </div>
                <div class="filter-box">
                    <select v-model="selectedCategory" class="filter-select">
                        <option value="">전체</option>
                        <option value="general">일반</option>
                        <option value="question">질문</option>
                        <option value="info">정보</option>
                    </select>
                </div>
            </div>

            <!-- 게시판 헤더 -->
            <div class="board-table">
                <div class="table-header">
                    <div class="col-number">번호</div>
                    <div class="col-content">내용</div>
                </div>

                <!-- 게시글 리스트 -->
                <div class="table-body" :class="{ 'compact': filteredPosts.length <= 5 }">
                    <div 
                        v-for="(post, index) in paginatedPosts" 
                        :key="post.id"
                        class="table-row-container"
                        @click="viewPost(post)"
                    >
                        <!-- 번호 (rowspan 역할) -->
                        <div class="col-number-cell">
                            {{ getTotalNumber(index) }}
                        </div>
                        
                        <!-- 내용 영역 -->
                        <div class="col-content-cell">
                            <!-- 첫째 줄: 제목 -->
                            <div class="content-row title-row">
                                <span class="post-title">{{ post.title }}</span>
                                <span v-if="post.isNew" class="new-badge">NEW</span>
                            </div>
                            <!-- 둘째 줄: 작성자, 게시일, 조회수 -->
                            <div class="content-row meta-row">
                                <div class="meta-left">
                                    <span class="post-author">{{ post.author }}</span>
                                </div>
                                <div class="meta-right">
                                    <span class="post-date">{{ formatDate(post.createdAt) }}</span>
                                    <span class="post-views">조회 {{ post.views }}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <!-- 빈 상태 -->
                <div v-if="filteredPosts.length === 0" class="empty-state">
                    <p class="empty-message">검색 결과가 없습니다</p>
                </div>
            </div>

            <!-- 개선된 페이지네이션 -->
            <div class="pagination" v-if="totalPages > 1">
                <!-- 이전 버튼 -->
                <button 
                    @click="goToPage(currentPage - 1)"
                    :disabled="currentPage === 1"
                    class="page-btn nav-btn"
                >
                    ‹ 이전
                </button>
                
                <!-- 첫 페이지 -->
                <button 
                    v-if="startPage > 1"
                    @click="goToPage(1)"
                    class="page-btn"
                >
                    1
                </button>
                
                <!-- 생략 표시 -->
                <span v-if="startPage > 2" class="page-dots">...</span>
                
                <!-- 페이지 번호들 -->
                <button 
                    v-for="page in visiblePages" 
                    :key="page"
                    @click="goToPage(page)"
                    :class="['page-btn', { active: currentPage === page }]"
                >
                    {{ page }}
                </button>
                
                <!-- 생략 표시 -->
                <span v-if="endPage < totalPages - 1" class="page-dots">...</span>
                
                <!-- 마지막 페이지 -->
                <button 
                    v-if="endPage < totalPages"
                    @click="goToPage(totalPages)"
                    class="page-btn"
                >
                    {{ totalPages }}
                </button>
                
                <!-- 다음 버튼 -->
                <button 
                    @click="goToPage(currentPage + 1)"
                    :disabled="currentPage === totalPages"
                    class="page-btn nav-btn"
                >
                    다음 ›
                </button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'FreeBoard',
    data() {
        return {
            searchKeyword: '',
            selectedCategory: '',
            currentPage: 1,
            itemsPerPage: 10,
            maxVisiblePages: 5,
            posts: [
                {
                    id: 1,
                    title: 'Vue.js 개발 팁 공유드립니다 - 컴포넌트 재사용성과 성능 최적화에 대한 실무 경험',
                    author: '개발자123',
                    category: 'info',
                    createdAt: '2025-06-15',
                    views: 152,
                    isNew: true
                },
                {
                    id: 2,
                    title: '주말에 뭐하시나요? 개발자들의 휴식 방법에 대해 궁금합니다',
                    author: 'pikolo',
                    category: 'general',
                    createdAt: '2025-06-15',
                    views: 89,
                    isNew: true
                },
                {
                    id: 3,
                    title: 'Docker 컨테이너 관련 질문있습니다 - 프로덕션 환경에서의 최적화 방법',
                    author: '초보개발자',
                    category: 'question',
                    createdAt: '2025-06-14',
                    views: 234,
                    isNew: false
                },
                {
                    id: 4,
                    title: 'SpringBoot 프로젝트 시작하기 - 초기 설정부터 배포까지의 전체 과정',
                    author: '백엔드마스터',
                    category: 'info',
                    createdAt: '2025-06-14',
                    views: 345,
                    isNew: false
                },
                {
                    id: 5,
                    title: '오늘 날씨 정말 좋네요! 개발하기 딱 좋은 날씨입니다',
                    author: '날씨맨',
                    category: 'general',
                    createdAt: '2025-06-13',
                    views: 67,
                    isNew: false
                },
                {
                    id: 6,
                    title: 'CSS 애니메이션 구현 방법 - 부드러운 트랜지션과 키프레임 활용법',
                    author: '프론트개발자',
                    category: 'info',
                    createdAt: '2025-06-13',
                    views: 123,
                    isNew: false
                },
                {
                    id: 7,
                    title: '데이터베이스 최적화 팁 - 인덱스 설계와 쿼리 성능 향상 방법',
                    author: 'DB전문가',
                    category: 'info',
                    createdAt: '2025-06-12',
                    views: 189,
                    isNew: false
                },
                {
                    id: 8,
                    title: '개발자 모임 참여하실분? 매주 토요일 스터디 모임 진행합니다',
                    author: '모임장',
                    category: 'general',
                    createdAt: '2025-06-12',
                    views: 78,
                    isNew: false
                },
                {
                    id: 9,
                    title: 'API 설계 관련 질문 - RESTful API 베스트 프랙티스에 대해',
                    author: '신입개발자',
                    category: 'question',
                    createdAt: '2025-06-11',
                    views: 145,
                    isNew: false
                },
                {
                    id: 10,
                    title: '프로젝트 배포 후기 - AWS EC2와 RDS를 활용한 실제 서비스 운영 경험',
                    author: '프로젝트리더',
                    category: 'info',
                    createdAt: '2025-06-11',
                    views: 267,
                    isNew: false
                },
                {
                    id: 11,
                    title: '커피 추천 부탁드려요 - 개발할 때 마시기 좋은 커피',
                    author: '커피러버',
                    category: 'general',
                    createdAt: '2025-06-10',
                    views: 45,
                    isNew: false
                },
                {
                    id: 12,
                    title: 'Git 브랜치 전략 궁금합니다 - Git Flow vs GitHub Flow 비교',
                    author: '버전관리초보',
                    category: 'question',
                    createdAt: '2025-06-10',
                    views: 156,
                    isNew: false
                }
            ]
        }
    },
    computed: {
        filteredPosts() {
            let filtered = this.posts;
            
            if (this.searchKeyword) {
                filtered = filtered.filter(post => 
                    post.title.toLowerCase().includes(this.searchKeyword.toLowerCase()) ||
                    post.author.toLowerCase().includes(this.searchKeyword.toLowerCase())
                );
            }
            
            if (this.selectedCategory) {
                filtered = filtered.filter(post => post.category === this.selectedCategory);
            }
            
            return filtered;
        },
        totalPages() {
            return Math.ceil(this.filteredPosts.length / this.itemsPerPage);
        },
        paginatedPosts() {
            const start = (this.currentPage - 1) * this.itemsPerPage;
            const end = start + this.itemsPerPage;
            return this.filteredPosts.slice(start, end);
        },
        startPage() {
            const half = Math.floor(this.maxVisiblePages / 2);
            let start = this.currentPage - half;
            
            if (start < 1) start = 1;
            if (start + this.maxVisiblePages - 1 > this.totalPages) {
                start = this.totalPages - this.maxVisiblePages + 1;
            }
            if (start < 1) start = 1;
            
            return start;
        },
        endPage() {
            let end = this.startPage + this.maxVisiblePages - 1;
            if (end > this.totalPages) end = this.totalPages;
            return end;
        },
        visiblePages() {
            const pages = [];
            for (let i = this.startPage; i <= this.endPage; i++) {
                pages.push(i);
            }
            return pages;
        }
    },
    methods: {
        viewPost(post) {
            console.log('게시글 보기:', post);
        },
        formatDate(dateString) {
            const date = new Date(dateString);
            return `${String(date.getMonth() + 1).padStart(2, '0')}.${String(date.getDate()).padStart(2, '0')}`;
        },
        getTotalNumber(index) {
            return this.filteredPosts.length - ((this.currentPage - 1) * this.itemsPerPage + index);
        },
        goToPage(page) {
            if (page >= 1 && page <= this.totalPages) {
                this.currentPage = page;
            }
        }
    }
}
</script>

<style scoped>
.board-page {
    min-height: 100vh;
    background: #f8f9fa;
    padding: 2rem 0;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1rem;
}

/* 헤더 */
.board-header {
    text-align: center;
    margin-bottom: 3rem;
    margin-top: 60px;
    padding: 2rem 0;
}

.page-title {
    font-size: 2.5rem;
    font-weight: 300;
    color: #212529;
    margin-bottom: 0.5rem;
    letter-spacing: -0.02em;
}

/* 컨트롤 영역 */
.board-controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
    gap: 1rem;
}

.search-box {
    flex: 1;
    max-width: 400px;
}

.search-input {
    width: 100%;
    padding: 0.75rem 1rem;
    border: 1px solid #e9ecef;
    border-radius: 8px;
    font-size: 0.95rem;
    background: #ffffff;
    transition: all 0.2s ease;
}

.search-input:focus {
    outline: none;
    border-color: #6c757d;
    box-shadow: 0 0 0 3px rgba(108, 117, 125, 0.1);
}

.filter-box {
    min-width: 150px;
}

.filter-select {
    width: 100%;
    padding: 0.75rem 1rem;
    border: 1px solid #e9ecef;
    border-radius: 8px;
    background: #ffffff;
    font-size: 0.95rem;
    cursor: pointer;
    transition: border-color 0.2s ease;
}

.filter-select:focus {
    outline: none;
    border-color: #6c757d;
}

/* 테이블 스타일 */
.board-table {
    background: #ffffff;
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
    overflow: hidden;
    margin-bottom: 2rem;
}

.table-header {
    display: flex;
    background: #f8f9fa;
    border-bottom: 2px solid #e9ecef;
    font-weight: 600;
    color: #495057;
    font-size: 0.9rem;
}

.table-header .col-number {
    flex: 0 0 80px;
    padding: 1rem 0.75rem;
    text-align: center;
}

.table-header .col-content {
    flex: 1;
    padding: 1rem 0.75rem;
    text-align: center;
}

.table-body {
    min-height: auto;
}

/* 2줄 레이아웃 컨테이너 */
.table-row-container {
    display: flex;
    border-bottom: 1px solid #f1f3f4;
    cursor: pointer;
    transition: all 0.2s ease;
    min-height: 80px;
}

.table-row-container:last-child {
    border-bottom: none;
}

.table-row-container:hover {
    background: #f8f9fa;
}

/* 번호 셀 (rowspan 효과) */
.col-number-cell {
    flex: 0 0 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #6c757d;
    font-weight: 600;
    font-size: 0.9rem;
    background: #fafbfc;
    border-right: 1px solid #f1f3f4;
}

/* 내용 셀 */
.col-content-cell {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 0.75rem 1rem;
}

/* 내용 행들 */
.content-row {
    display: flex;
    align-items: center;
    min-height: 24px;
}

/* 제목 행 */
.title-row {
    margin-bottom: 0.5rem;
    flex: 1;
    align-items: flex-start;
    padding-top: 0.25rem;
}

.post-title {
    color: #212529;
    font-weight: 500;
    font-size: 1rem;
    line-height: 1.4;
    flex: 1;
}

.table-row-container:hover .post-title {
    color: #495057;
}

.new-badge {
    display: inline-block;
    background: #28a745;
    color: white;
    font-size: 0.7rem;
    padding: 0.15rem 0.4rem;
    border-radius: 3px;
    margin-left: 0.5rem;
    font-weight: 500;
    flex-shrink: 0;
}

/* 메타 행 */
.meta-row {
    justify-content: space-between;
    font-size: 0.85rem;
    color: #6c757d;
    margin-top: auto;
}

.meta-left {
    display: flex;
    align-items: center;
}

.meta-right {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.post-author {
    font-weight: 500;
}

.post-date, .post-views {
    white-space: nowrap;
}

/* 빈 상태 */
.empty-state {
    text-align: center;
    padding: 3rem;
    color: #adb5bd;
}

.empty-message {
    font-size: 1rem;
    margin: 0;
}

/* 페이지네이션 스타일 */
.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.25rem;
    margin-top: 1rem;
}

.page-btn {
    padding: 0.5rem 0.75rem;
    border: 1px solid #e9ecef;
    background: #ffffff;
    color: #6c757d;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.2s ease;
    font-size: 0.9rem;
    min-width: 40px;
}

.page-btn:hover:not(:disabled) {
    background: #f8f9fa;
    border-color: #6c757d;
}

.page-btn.active {
    background: #6c757d;
    color: white;
    border-color: #6c757d;
}

.page-btn:disabled {
    background: #f8f9fa;
    color: #adb5bd;
    cursor: not-allowed;
    border-color: #e9ecef;
}

.nav-btn {
    padding: 0.5rem 1rem;
    font-weight: 500;
}

.page-dots {
    color: #6c757d;
    padding: 0.5rem 0.25rem;
    font-size: 0.9rem;
}

/* 반응형 */
@media (max-width: 768px) {
    .board-page {
        padding: 1rem 0;
    }
    
    .page-title {
        font-size: 2rem;
    }
    
    .board-controls {
        flex-direction: column;
        align-items: stretch;
    }
    
    .search-box {
        max-width: none;
    }
    
    .table-header .col-number {
        flex: 0 0 60px;
    }
    
    .col-number-cell {
        flex: 0 0 60px;
    }
    
    .col-content-cell {
        padding: 0.5rem 0.75rem;
    }
    
    .post-title {
        font-size: 0.9rem;
    }
    
    .meta-row {
        font-size: 0.8rem;
    }
    
    .meta-right {
        gap: 0.5rem;
    }
    
    .pagination {
        gap: 0.15rem;
    }
    
    .page-btn {
        padding: 0.4rem 0.6rem;
        font-size: 0.85rem;
        min-width: 35px;
    }
    
    .nav-btn {
        padding: 0.4rem 0.8rem;
    }
}

/* 접근성 */
.table-row-container:focus {
    outline: 2px solid #6c757d;
    outline-offset: -2px;
}

.search-input::placeholder {
    color: #adb5bd;
}

/* 로딩 상태 */
.table-row-container.loading {
    opacity: 0.6;
    pointer-events: none;
}
</style>
